import { Component, Event, EventEmitter, h, Prop, State } from '@stencil/core';
import { ButtonSize } from '../at-button/at-button';
import { TreeNode } from '../../types/tree';

/**
 * @category Navigation
 * @description A tree component for displaying hierarchical data.
 */

/**
 * ### ```interface ItemNode```
 * ```
 *  id: string;
 *  displayName: string;
 *  children: ItemNode[];
 *  isSelected?: boolean;
 *  tooltip?: string;
 * ```
 */
@Component({
    tag: 'at-tree',
    shadow: false,
})
export class AtTreeComponent {
    /**
     * List of items to be displayed in the tree
     */
    @Prop({ mutable: true })
    item_list: TreeNode[];

    /**
     * Set the size of the tree button, default sm
     */
    @Prop()
    size: ButtonSize = 'sm';

    /**
     * Emits when the button is clicked
     */
    @Event()
    atuiClick: EventEmitter<TreeNode>;

    @State() flattenedItemList: Array<TreeNode> = [];
    @State() flattenedItemStyles = [];

    handleClick = (id: string) => {
        let clickedItem: TreeNode | null = null;

        const updateIsSelected = (items: TreeNode[]): TreeNode[] => {
            const tempItems: TreeNode[] = [];

            items.forEach((item) => {
                const tempItem = { ...item };

                if (item.id === id) {
                    tempItem.selected = !tempItem.selected;
                    clickedItem = tempItem;
                }

                if (item.children) {
                    tempItem.children = updateIsSelected(item.children);
                }

                tempItems.push(tempItem);
            });

            return tempItems;
        };

        this.item_list = updateIsSelected(this.item_list);
        this.flattenItemList(this.item_list);

        if (clickedItem) {
            this.atuiClick.emit(clickedItem);
        }
    };

    flattenItemList = (itemList: TreeNode[]) => {
        const tempItemList: TreeNode[] = [];

        const addToFlattenedAndCheckChildren = (
            items: TreeNode[],
            layer: number,
        ) => {
            items.forEach((item) => {
                const itemWithDepth = { ...item, depth: layer };
                tempItemList.push(itemWithDepth);

                if (item.selected && item.children && item.children.length) {
                    addToFlattenedAndCheckChildren(item.children, layer + 1);
                }
            });
        };

        addToFlattenedAndCheckChildren(itemList, 0);
        this.flattenedItemList = tempItemList;
    };

    componentWillRender() {
        if (this.item_list && this.item_list.length) {
            this.flattenItemList(this.item_list);
        }
    }

    render() {
        return (
            <div class="w-full">
                <slot />
                {this.flattenedItemList &&
                    this.flattenedItemList.map((item, index) => (
                        <div
                            key={index}
                            class={`flex w-full`}
                        >
                            {item.tooltip !== undefined ? (
                                <at-tooltip
                                    position="right"
                                    class="flex flex-grow items-start"
                                >
                                    <at-tree-item
                                        class="flex-grow"
                                        depth={item.depth}
                                        slot="tooltip-trigger"
                                        onAtuiClick={() =>
                                            this.handleClick(item.id)
                                        }
                                        key={item.id}
                                        label={item.displayName}
                                        size={this.size}
                                        has_children={
                                            item.children &&
                                            item.children.length > 0
                                        }
                                        selected={item.selected}
                                    >
                                        <slot name="item-content"></slot>
                                    </at-tree-item>

                                    <span>{item.tooltip}</span>
                                </at-tooltip>
                            ) : (
                                <at-tree-item
                                    depth={item.depth}
                                    style={{
                                        paddingLeft:
                                            this.flattenedItemStyles[index] +
                                            'px',
                                    }}
                                    onAtuiClick={() =>
                                        this.handleClick(item.id)
                                    }
                                    has_children={
                                        item.children &&
                                        item.children.length > 0
                                    }
                                    key={item.id}
                                    label={item.displayName}
                                    size={this.size}
                                >
                                    <slot name="item-content"></slot>
                                </at-tree-item>
                            )}
                        </div>
                    ))}
            </div>
        );
    }
}
